import { Component } from '@angular/core';
import {MatDividerModule} from '@angular/material/divider';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'my-resume';
  show_what="Show More";
  show_what_pro="Show More";
  constructor(){}

  ngOnInit(){
    setInterval(()=>{
      this.check_width("main",1000);
      this.check_width("resume_body",1000);
    },1000)
  }
  public getPageHeight(){
    let body = document.body;
    let html = document.documentElement;
    let height:number;
    let h=Math.max(html.clientHeight,window.innerHeight);
    if(h>0){
      height=h-0.002*h;
    }else{
      height=1080;
    }
    console.log(height)
    return height;
  }
  public check_width(id,w){
    let obj=document.getElementById(id);
    if(obj.clientWidth > w){
      obj.style.flexDirection='row'
    }else{
      obj.style.flexDirection='column'
    }
    // let mdiv2=document.getElementsByClassName('main')
    // let h1=document.getElementsByClassName('header1');
  }
  public toggleMoreLess(){
    if(this.show_what=="Show More"){
      this.show_what="Show Less"
      let obj=document.getElementById('showMore');
      obj.style.display="flex";
      let obj1=document.getElementById('showLess');
      obj1.style.display="none";
    }else{
      this.show_what="Show More";
      let obj=document.getElementById('showLess');
      obj.style.display="flex";
      let obj1=document.getElementById('showMore');
      obj1.style.display="none";
    }
  }
  public check_show_more(){
    if(this.show_what=='Show More'){
      return true
    }else{
      return false
    }
  }
  public toggleMoreLessPro(){
    if(this.show_what_pro=="Show More"){
      this.show_what_pro="Show Less"
      let obj=document.getElementById('showMorePro');
      obj.style.display="flex";
      let obj1=document.getElementById('showLessPro');
      obj1.style.display="none";
    }else{
      this.show_what_pro="Show More";
      let obj=document.getElementById('showLessPro');
      obj.style.display="flex";
      let obj1=document.getElementById('showMorePro');
      obj1.style.display="none";
    }
  }
  public check_show_more_pro(){
    if(this.show_what_pro=='Show More'){
      return true
    }else{
      return false
    }
  }
}
